@import url("https://fonts.googleapis.com/css?family=Oswald:400,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Lato:400,700&display=swap");
@font-face {
  font-family: 'icomoon';
  src: url("/fonts/icomoon.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: block; }

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-clock:before {
  content: "\e94e"; }

.icon-bell:before {
  content: "\e951"; }

.icon-calendar:before {
  content: "\e953"; }

* {
  box-sizing: border-box; }

html {
  font-size: 15px;
  font-family: "Oswald", sans-serif; }

body {
  margin: 0; }

.sidebar {
  position: fixed;
  color: #ed683c;
  width: 16rem;
  height: 100vh;
  padding: 1rem 2rem;
  background-color: #1f1f1f;
  font-family: "Lato", sans-serif;
  font-weight: bold;
  text-align: center; }
  .sidebar p {
    color: #cdcdcd; }
  .sidebar__logo {
    background-image: url("/img/vibecast-logo.svg");
    background-size: contain;
    width: 12rem;
    height: 12rem; }
  .sidebar__link {
    text-decoration: none;
    color: #ed683c; }

.sidebar__navigation a {
  margin: 1em 1.5em;
  line-height: 1.7;
  text-transform: uppercase; }

.sidebar__desc {
  margin: 2em 0; }

.podcast {
  margin-left: 16rem; }

.podcast-heading {
  display: flex;
  align-items: center;
  height: 5rem;
  padding: 1rem 2.5rem;
  background-color: #f0f0f0; }
  .podcast-heading__logo {
    margin-right: 1rem; }
    .podcast-heading__logo img {
      width: 3rem;
      height: 3rem; }
  .podcast-heading__title {
    padding-bottom: 0.5rem;
    font-size: 2rem;
    line-height: 2rem;
    text-transform: uppercase; }

.episode {
  display: flex;
  align-items: center;
  border-top: 1px solid grey;
  padding: 0.8em;
  color: #cdcdcd; }
  .episode__wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 40%; }
  .episode__title {
    margin-left: 1rem;
    font-size: 1.8rem;
    text-transform: uppercase;
    color: #1f1f1f; }
  .episode__number {
    font-size: 3em;
    font-weight: bold; }
  .episode__thumbnail {
    height: 2.6em;
    width: 2.6em;
    background-color: black;
    margin: 0 1em; }
  .episode__date {
    margin: 0 1em; }
  .episode i {
    margin-right: 0.3em; }

.btn {
  outline: none;
  border-radius: 5px;
  font-size: 0.8em;
  padding: 0.4em 0.7em;
  margin: 2em;
  border: 1px white solid;
  background-color: #2688dc;
  color: #cdcdcd; }
  .btn i {
    font-size: 1.1em;
    padding: 0.2em; }
